
'use client';

import { useState } from 'react';
import { useAuth } from '@/contexts/AuthContext';
import { DayPicker } from 'react-day-picker';
import 'react-day-picker/dist/style.css';
import { es } from 'date-fns/locale';
import { solicitarTurnoPeluqueria } from '@/app/actions/turnosActions';

export default function FormularioTurnoPeluqueria({ mascotas }) {
    const { user } = useAuth();
    const [mascota, setMascota] = useState('');
    const [fecha, setFecha] = useState(null);
    const [turno, setTurno] = useState(''); // 'manana' o 'tarde'
    const [servicios, setServicios] = useState({
        cepillado: false,
        rapado: false,
        corteTijera: false,
    });
    const [transporte, setTransporte] = useState(false); // Nuevo estado para el transporte
    const [error, setError] = useState('');
    const [success, setSuccess] = useState('');
    const [loading, setLoading] = useState(false);

    const handleServicioChange = (event) => {
        const { name, checked } = event.target;
        setServicios(prev => ({ ...prev, [name]: checked }));
    };

    const handleSubmit = async (event) => {
        event.preventDefault();
        if (!user) {
            setError('Tu sesión ha expirado, por favor, recarga la página.');
            return;
        }
        if (!mascota || !fecha || !turno) {
            setError('Por favor, completa todos los campos: mascota, fecha y turno.');
            return;
        }

        setLoading(true);
        setError('');
        setSuccess('');

        const servicesArray = Object.keys(servicios).filter(key => servicios[key]);
        
        const turnoData = {
            clienteId: user.uid,
            mascotaId: mascota,
            fecha: fecha.toISOString().split('T')[0],
            turno,
            servicios: servicesArray,
            transporte: transporte, // Se añade el valor de transporte
            tipo: 'peluqueria',
        };

        try {
            const result = await solicitarTurnoPeluqueria(turnoData);
            if (result.success) {
                setSuccess('¡Solicitud de turno enviada con éxito! Recibirás una confirmación por la misma vía.');
                // Reset form
                setMascota('');
                setFecha(null);
                setTurno('');
                setServicios({ cepillado: false, rapado: false, corteTijera: false });
                setTransporte(false); // Reset del transporte
            } else {
                setError(result.error || 'No se pudo procesar la solicitud.');
            }
        } catch (err) {
            setError('Ocurrió un error inesperado al comunicarnos con el servidor.');
        } finally {
            setLoading(false);
        }
    };

    const today = new Date();
    const disabledDays = [
        { dayOfWeek: [0, 6] }, // Deshabilita Sábados (6) y Domingos (0)
        { before: today }
    ];

    return (
        <form onSubmit={handleSubmit} className="max-w-xl mx-auto bg-white p-8 rounded-2xl shadow-lg space-y-6">
            <h2 className="text-2xl font-bold text-center text-gray-800">Solicitar Turno de Peluquería</h2>

            {error && <p className="text-sm text-red-600 bg-red-100 p-3 rounded-md text-center">{error}</p>}
            {success && <p className="text-sm text-green-600 bg-green-100 p-3 rounded-md text-center">{success}</p>}

            <div>
                <label htmlFor="mascota" className="block text-sm font-medium text-gray-700 mb-1">Elige tu mascota</label>
                <select id="mascota" value={mascota} onChange={(e) => setMascota(e.target.value)} required className="w-full input">
                    <option value="">Selecciona una mascota</option>
                    {mascotas.map(m => (
                        <option key={m.id} value={m.id}>{m.nombre} ({m.tamaño})</option>
                    ))}
                </select>
            </div>

            <div className="flex flex-col items-center">
                <label className="block text-sm font-medium text-gray-700 mb-2">Selecciona el día</label>
                <DayPicker
                    mode="single"
                    selected={fecha}
                    onSelect={setFecha}
                    locale={es}
                    disabled={disabledDays} // Se aplica la nueva regla de días deshabilitados
                    className="bg-violet-50 p-4 rounded-lg"
                    modifiersClassNames={{
                        selected: 'bg-violet-600 text-white',
                        today: 'text-violet-700 font-bold',
                    }}
                />
            </div>
            
            <div>
                 <label className="block text-sm font-medium text-gray-700 mb-2">Selecciona el turno</label>
                 <div className="grid grid-cols-2 gap-4">
                    <button type="button" onClick={() => setTurno('manana')} className={`py-3 rounded-lg text-sm font-semibold transition-colors ${turno === 'manana' ? 'bg-violet-600 text-white shadow-md' : 'bg-gray-200 text-gray-700'}`}>
                        Turno Mañana (8hs - 12hs)
                    </button>
                    <button type="button" onClick={() => setTurno('tarde')} className={`py-3 rounded-lg text-sm font-semibold transition-colors ${turno === 'tarde' ? 'bg-violet-600 text-white shadow-md' : 'bg-gray-200 text-gray-700'}`}>
                        Turno Tarde (13hs - 17hs)
                    </button>
                 </div>
            </div>

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Servicios Adicionales</label>
                <div className="space-y-2">
                    <div className="flex items-center">
                        <input id="cepillado" name="cepillado" type="checkbox" checked={servicios.cepillado} onChange={handleServicioChange} className="h-4 w-4 text-violet-600 focus:ring-violet-500 border-gray-300 rounded" />
                        <label htmlFor="cepillado" className="ml-3 block text-sm text-gray-800">Cepillado</label>
                    </div>
                    <div className="flex items-center">
                        <input id="rapado" name="rapado" type="checkbox" checked={servicios.rapado} onChange={handleServicioChange} className="h-4 w-4 text-violet-600 focus:ring-violet-500 border-gray-300 rounded" />
                        <label htmlFor="rapado" className="ml-3 block text-sm text-gray-800">Rapado Parejo</label>
                    </div>
                    <div className="flex items-center">
                        <input id="corteTijera" name="corteTijera" type="checkbox" checked={servicios.corteTijera} onChange={handleServicioChange} className="h-4 w-4 text-violet-600 focus:ring-violet-500 border-gray-300 rounded" />
                        <label htmlFor="corteTijera" className="ml-3 block text-sm text-gray-800">Corte a Tijera</label>
                    </div>
                </div>
            </div>

            <div>
                <div className="flex items-center">
                    <input id="transporte" name="transporte" type="checkbox" checked={transporte} onChange={(e) => setTransporte(e.target.checked)} className="h-4 w-4 text-violet-600 focus:ring-violet-500 border-gray-300 rounded" />
                    <label htmlFor="transporte" className="ml-3 block text-sm font-medium text-gray-800">¿Necesitas transporte?</label>
                </div>
            </div>

            <button type="submit" disabled={loading || !fecha || !turno || !mascota} className="w-full flex justify-center py-3 px-4 border border-transparent rounded-full shadow-sm text-sm font-medium text-white bg-violet-600 hover:bg-violet-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-violet-500 disabled:bg-gray-400 disabled:cursor-not-allowed">
                {loading ? 'Enviando Solicitud...' : 'Solicitar Turno'}
            </button>
        </form>
    );
}
